import List from "./List";
import data from "./data";
import { useState } from "react";

function App() {
  const [sites, setSites] = useState(data);
  return (
    <main>
      <section>
        <div className="title">
          <h2>AWS projects</h2>
          <div className="underline"></div>
        </div>
        <div>
          {sites.map((site) => {
            return <List key={site.id} {...site} />;
          })}
        </div>
      </section>
    </main>
  );
}

export default App;
