import React from "react";

function List({ id, name, link, image }) {
  return (
    <article className="single-tour">
      <img src={image} alt={name} />
      <footer>
        <div className="tour-info">
          <h4>{name}</h4>

          <h4>
            <a href={link}>Visit </a>
          </h4>
        </div>
      </footer>
    </article>
  );
}
export default List;
